import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ImageWrapper from '../../components/ImageWrapper/ImageWrapper';
import classes from './NewRule.module.css';

class NewRule extends Component {

    state = {
        ruleText: ''
    }

    onSubmitHandler = e => {
        e.preventDefault();

        console.log(this.state.ruleText);

    }

    render() {
        return (
            <div>
                <ImageWrapper />

                <div className={classes.menu}>
                    <ul>
                        <li><Link to="/">Home</Link></li>
                    </ul>
                </div>

                <hr />

                <h3 style={{ textAlign: 'center' }}>Add new rule</h3>

                <form onSubmit={this.onSubmitHandler}>
                    <div className="container">
                        <div className="row-12">
                            <textarea
                                className="form-control"
                                id="ruleText"
                                rows="20"
                                value={this.state.ruleText}
                                onChange={e => this.setState({ruleText: e.target.value})}></textarea>
                        </div>
                    </div>

                    <div className="container">
                        <div className="row-12" style={{ margin: '20px auto' }}>
                            <button className="btn btn-primary" type="submit" style={{ float: 'right', marginBottom: '50px' }}>Submit</button>
                        </div>
                    </div>

                </form>

            </div>
        );
    }
}

export default NewRule;