import React, { Component } from 'react';
import classes from './NewUser.module.css';
import ImageWrapper from '../../components/ImageWrapper/ImageWrapper';
import { Link } from 'react-router-dom';
import axios from '../../axios';

class NewUser extends Component {

    state = {
        username: '',
        password: '',
        role: 1
    }

    onSubmit = e => {
        e.preventDefault();

        let user = {...this.state};

        axios.post('api/register', user)
            .then(res => {
                console.log(res);
                alert("User registered successfully.");
            })
            .catch(err => {
                console.log(err);
                alert("Error");
            });

        console.log(this.state);
    }


    render() {
        return (
            <div>
                <ImageWrapper />

                <div className={classes.menu}>
                    <ul>
                        <li><Link to="/">Home</Link></li>
                    </ul>
                </div>

                <hr />

                <div className="container" style={{ margin: 'auto', textAlign: 'center' }}>
                    <h5>Register new Doctor or Administrator</h5>

                    <div className="container">
                        <div className="row">
                            <div className={classes.Form + " col"}>
                                <form onSubmit={this.onSubmit}>
                                    <div className="form-group">
                                        <label htmlFor="username">Username</label>
                                        <input
                                            type="text"
                                            className="form-control"
                                            id="username"
                                            placeholder="Enter username"
                                            value={this.state.username}
                                            onChange={e => this.setState({username: e.target.value})}
                                        />
                                    </div>

                                    <div className="form-group">
                                        <label htmlFor="password">Password</label>
                                        <input
                                            type="password"
                                            className="form-control"
                                            id="password"
                                            placeholder="Enter password"
                                            value={this.state.password}
                                            onChange={e => this.setState({password: e.target.value})}
                                        />
                                    </div>

                                    <div className="form-group">
                                        <label htmlFor="role">Role select</label>
                                        <select className="form-control" id="role" onChange={e => this.setState({role: e.target.value})}>
                                            <option value="1" defaultValue>Doctor</option>
                                            <option value="2">Administrator</option>
                                        </select>
                                    </div>

                                    <br />

                                    <button type="submit" className="btn btn-primary">Register</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div >
        );
    }
}

export default NewUser;