import React, { Component } from 'react';
import ImageWrapper from '../../components/ImageWrapper/ImageWrapper';
import classes from './Country.module.css';
import { Link } from 'react-router-dom';
import NewCountryForm from './NewCountryForm/NewCountryForm';
import CountryTable from './CountryTable/CountryTable';
import Modal from '../../components/UI/Modal/Modal';
import EditCountryForm from './EditCountryForm/EditCountryForm';
import axios from '../../axios';


class Country extends Component {

    state = {
        countries: null,
        modalOpen: false,
        selectedCountry: null
    }

    componentDidMount() {
        axios.get("country/get/all")
            .then(res => {
                this.setState({ countries: res.data });
            })
            .catch(err => console.log(err));
    }

    openModal = (country) => {
        let newCountry = {...country};
        this.setState({ modalOpen: true, selectedCountry: newCountry });
    }

    closeModal = () => {
        this.setState({ modalOpen: false });
    }

    addNewCountry = (newCountry) => {
        let countries = [...this.state.countries];
        countries.push(newCountry);
        this.setState({ countries: countries });
    }

    updateCountry = country => {
        for (let i = 0; i < this.state.countries.length; i++) {
            if(country.id === this.state.countries[i].id){
                let countries = [...this.state.countries];
                countries[i] = country;
                this.setState({countries: countries});
            }
        }
        this.closeModal();
    }

    render() {

        console.log(this.state.selectedCountry);

        return (
            <div>
                <ImageWrapper />
                <div className={classes.menu}>
                    <ul>
                        <li><Link to="/">Home</Link></li>
                    </ul>
                </div>

                <hr />

                <div className="container">
                    <div className="row">
                        <div className={classes.Form + " col"}>
                            <h5 style={{ textAlign: "center" }}>Add new country</h5>
                            <NewCountryForm addNewCountry={this.addNewCountry} />
                        </div>
                        <div className="col">
                            <h5 style={{ textAlign: "center" }}>Country list</h5>
                            <CountryTable openModal={this.openModal} countries={this.state.countries} />
                        </div>
                    </div>
                </div>

                <Modal show={this.state.modalOpen} modalClosed={() => this.closeModal()}>
                    {this.state.selectedCountry ? <EditCountryForm
                        country={this.state.selectedCountry}
                        updateCountry={this.updateCountry}
                        closeModal={this.closeModal}
                    /> : null}
                </Modal>
            </div>
        );
    }
}

export default Country;