import React from 'react';
import classes from './Input.module.css';

const Input = (props) => {

    let inputElement = null;

    const inputClasses = [classes.inputElement];

    if (props.invalid && props.shouldValidate && props.touched) {
        inputClasses.push(classes.Invalid);
    }

    switch (props.elementType) {
        case ('input'):
            inputElement = <input
                className={inputClasses.join(' ') +  props.elementConfig.type === 'checkbox' ? null : " form-control"}
                style={props.elementConfig.type === "checkbox" ? {marginTop: '13px'} : null}
                {...props.elementConfig}
                value={props.value}
                onChange={props.changed}
                id={props.id} />;
            break;
        case ('textarea'):
            inputElement = <textarea
                rows="3" cols="60"
                className={inputClasses.join(' ') + " form-control"}
                {...props.elementConfig}
                value={props.value}
                onChange={props.changed}
                id={props.id} />;
            break;
        case ('select'):
            inputElement = <select
                className={inputClasses.join(' ') + " form-control"}
                value={props.value}
                onChange={props.changed}
                id={props.id}>
                    <option value="" defaultValue hidden></option>
                {props.elementConfig.options.map(option => {
                    return <option value={option.value} key={option.value}>{option.displayValue}</option>
                })}
            </select>
            break;

        default:
            inputElement = <input
                className={inputClasses.join(' ') + " form-control"}
                {...props.elementConfig}
                value={props.value}
                onChange={props.changed}
                id={props.id} />
    }

    return (
        <div className="form-group row">
            <label className="col-form-label" htmlFor={props.id} style={{marginRight: '15px'}}>{props.label}</label>
            <div>
                {inputElement}
            </div>
        </div>
    );
};

export default Input;