import React, { Component } from 'react';
// import classes from './EditPatientForm.module.css';
import Input from '../../../components/UI/Input/Input';
import axios from '../../../axios';
import MultiSelect from "react-multi-select-component";


class EditPatientForm extends Component {

    state = {
        form: {
            name: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Firstname'
                },
                validation: {},
                value: this.props.patient.name,
                valid: true,
                touched: false,
                label: 'Firstname: ',
                show: false,
                loading: false
            },
            lastName: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Lastname'
                },
                validation: {},
                value: this.props.patient.lastName,
                valid: true,
                touched: false,
                label: 'Lastname: ',
                show: false,
                loading: false
            },
            countryId: {
                elementType: 'select',
                elementConfig: {
                    options: []
                },
                validation: {},
                value: this.props.patient.countryId,
                valid: true,
                touched: false,
                label: 'Country:',
                show: false,
                loading: false
            },
            covidPositiveContact: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.covidPositiveContact
                },
                validation: {},
                value: this.props.patient.covidPositiveContact,
                valid: true,
                touched: false,
                label: 'Had contact with COVID-19: ',
                show: false,
                loading: false
            },
            cold: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.hasCold
                },
                validation: {},
                value: this.props.patient.hasCold,
                valid: true,
                touched: false,
                label: 'Has cold: ',
                show: false,
                loading: false
            },
            lastFever: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Last fever'
                },
                validation: {},
                value: this.props.patient.lastFever,
                valid: true,
                touched: false,
                label: 'Last fever: ',
                show: false,
                loading: false
            },
            respiratoryRate: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Respiratory rate'
                },
                validation: {},
                value: this.props.patient.respiratoryRate,
                valid: true,
                touched: false,
                label: 'Respiratory rate: ',
                show: false,
                loading: false
            },
            hypoxia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.hasHypoxia
                },
                validation: {},
                value: this.props.patient.hasHypoxia,
                valid: true,
                touched: false,
                label: 'Has hypoxia: ',
                show: false,
                loading: false
            },
            soreThroat: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.hasSoreThroat
                },
                validation: {},
                value: this.props.patient.hasSoreThroat,
                valid: true,
                touched: false,
                label: 'Has sore throat: ',
                show: false,
                loading: false
            },
            cough: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.hasCough
                },
                validation: {},
                value: this.props.patient.hasCough,
                valid: true,
                touched: false,
                label: 'Has cough: ',
                show: false,
                loading: false
            },
            dyspnea: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.hasDyspnea
                },
                validation: {},
                value: this.props.patient.hasDyspnea,
                valid: true,
                touched: false,
                label: 'Has dyspnea: ',
                show: false,
                loading: false
            },
            tachypnea: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.hasTachypnea
                },
                validation: {},
                value: this.props.patient.hasTachypnea,
                valid: true,
                touched: false,
                label: 'Has tachypnea: ',
                show: false,
                loading: false
            },
            alc: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'ALC'
                },
                validation: {},
                value: this.props.patient.acl,
                valid: true,
                touched: false,
                label: 'Absolute lymphocite count: ',
                show: false,
                loading: false
            },
            pneumonia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.pneumonia
                },
                validation: {},
                value: this.props.patient.pneumonia,
                valid: true,
                touched: false,
                label: 'Has pneumonia: ',
                show: false,
                loading: false
            },
            oxygenSaturation: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Oxygen saturation'
                },
                validation: {},
                value: this.props.patient.oxygenSaturation,
                valid: true,
                touched: false,
                label: 'Oxygen saturation: ',
                show: false,
                loading: false
            },
            nonHospitalPneumonia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                    checked: this.props.patient.nonHospitalPneumonia
                },
                validation: {},
                value: this.props.patient.nonHospitalPneumonia,
                valid: true,
                touched: false,
                label: 'Has non hospital pneumonia: ',
                show: false,
                loading: false
            },
        },
        countriesVisited: [],
        countriesSelected: [],
        contactedPatients: [],
        patientsSelected: []
    }

    componentDidMount() {
        axios.get("country/get/all")
            .then(res => {
                const countryOptions = res.data.map(country => {
                    return {
                        value: country.id,
                        displayValue: country.countryName
                    };
                })
                const visitedCountries = res.data.map(country => {
                    return {
                        label: country.countryName,
                        value: country.id
                    };
                })
                const updatedForm = { ...this.state.form };
                const updatedElement = { ...updatedForm.countryId };
                const updatedElementConfig = { ...updatedElement.elementConfig };
                updatedElementConfig.options = [...countryOptions];
                updatedElement.elementConfig = updatedElementConfig;
                updatedForm.countryId = updatedElement;
                this.setState({ form: updatedForm, countriesVisited: visitedCountries }, () => {
                    let visitedCountries = [];

                    for (let i = 0; i < this.state.countriesVisited.length; i++) {
                        for (let j = 0; j < this.props.patient.countriesVisited.length; j++) {
                            if (this.state.countriesVisited[i].value === this.props.patient.countriesVisited[j]) {
                                visitedCountries.push(this.state.countriesVisited[i]);
                            }
                        }
                    }

                    this.setState({ countriesSelected: visitedCountries });

                });
            })
            .catch(err => console.log(err));

        axios.get("api/patient/get/all")
            .then(res => {
                const contactedPatients = res.data.map(p => {
                    return {
                        label: p.name + " " + p.lastName,
                        value: p.id
                    };
                })

                this.setState({ contactedPatients: contactedPatients }, () => {
                    let contactedPatient = [];

                    for (let i = 0; i < this.state.contactedPatients.length; i++) {
                        for (let j = 0; j < this.props.patient.contactedPatients.length; j++) {
                            if (this.state.contactedPatients[i].value === this.props.patient.contactedPatients[j]) {
                                contactedPatient.push(this.state.contactedPatients[i]);
                            }
                        }
                    }

                    console.log(contactedPatient);

                    this.setState({ patientsSelected: contactedPatient });
                });
            })
    }

    inputChangedHandler = (event, inputIdentifier) => {
        

        const updatedForm = { ...this.state.form };

        const updatedFormElement = { ...updatedForm[inputIdentifier] };

        if (updatedFormElement.elementConfig.type === "checkbox") {
            console.log("DSADSADSADSA");
            updatedFormElement.value = event.target.checked;
            updatedFormElement.elementConfig.checked = event.target.checked;
        } else {
            updatedFormElement.value = event.target.value;
        }
        updatedFormElement.touched = true;

        updatedForm[inputIdentifier] = updatedFormElement;

        this.setState({ form: updatedForm });
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        let formData = {};

        for (let formElementIdentifier in this.state.form) {
            formData[formElementIdentifier] = this.state.form[formElementIdentifier].value;
        }

        const cSelected = this.state.countriesSelected.map(c => c.value);

        const pSelected = this.state.patientsSelected.map(p => p.value);


        formData = { ...formData, countriesVisited: cSelected, contactedPatients: pSelected, id: this.props.patient.id };

        console.log(formData);

        axios.post('api/getCurringMeasures', formData)
        .then(res => {
            alert("Patient updated successfully.");
            window.location.reload();
        })
        .catch(err => console.log(err));

    }


    render() {

        const formElementsArray = [];

        for (let key in this.state.form) {
            formElementsArray.push({
                id: key,
                config: this.state.form[key]
            });
        }

        let form = (
            formElementsArray.map(formElement => {
                return <Input
                    key={formElement.id}
                    elementType={formElement.config.elementType}
                    elementConfig={formElement.config.elementConfig}
                    value={formElement.config.value}
                    changed={(event) => this.inputChangedHandler(event, formElement.id)}
                    invalid={!formElement.config.valid}
                    shouldValidate={formElement.config.validation}
                    touched={formElement.config.touched}
                    label={formElement.config.label}
                    id={formElement.id}
                />
            })
        );

        return (
            <div className="container">
                <form onSubmit={this.onFormSubmit}>
                    <h5 style={{ textAlign: 'center' }}>Edit Patient</h5>
                    <hr />
                    <div className="form-group">
                        <label htmlFor="editCountriesSelect">Countries visited:</label>
                        <div>
                            <MultiSelect
                                options={this.state.countriesVisited}
                                value={this.state.countriesSelected}
                                onChange={sel => this.setState({ countriesSelected: sel })}
                                labelledBy={"Visited countries"}
                                id="editCountriesSelect"
                            />
                        </div>
                    </div>

                    <div className="form-group">
                        <label htmlFor="editPatientsSelect">Patients contacted:</label>
                        <div>
                            <MultiSelect
                                options={this.state.contactedPatients}
                                value={this.state.patientsSelected}
                                onChange={sel => this.setState({ patientsSelected: sel })}
                                labelledBy={"Patients contacted"}
                                id="editPatientsSelect"
                            />
                        </div>
                    </div>
                    {form}
                    <hr />
                    <div className="row">
                        <button className="btn btn-primary" type="submit" style={{ margin: '0px 8px' }}>Accept</button>
                        <button type="button" className="btn btn-warning" style={{ margin: '0px 8px' }} onClick={() => window.location.reload()}>Back</button>
                    </div>
                </form>

            </div>
        );
    }
}

export default EditPatientForm;