import React, { Component } from 'react';
import Input from '../../../components/UI/Input/Input';
// import classes from './NewPatientForm.module.css';
import axios from '../../../axios';
import MultiSelect from "react-multi-select-component";

class NewPatientForm extends Component {

    state = {
        form: {
            name: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Firstname'
                },
                validation: {},
                value: '',
                valid: true,
                touched: false,
                label: 'Firstname: ',
                show: false,
                loading: false
            },
            lastName: {
                elementType: 'input',
                elementConfig: {
                    type: 'text',
                    placeholder: 'Lastname'
                },
                validation: {},
                value: '',
                valid: true,
                touched: false,
                label: 'Lastname: ',
                show: false,
                loading: false
            },
            countryId: {
                elementType: 'select',
                elementConfig: {
                    options: []
                },
                validation: {},
                value: '',
                valid: true,
                touched: false,
                label: 'Country:',
                show: false,
                loading: false
            },
            covidPositiveContact: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Had contact with COVID-19: ',
                show: false,
                loading: false
            },
            hasCold: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox',
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has cold: ',
                show: false,
                loading: false
            },
            lastFever: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Last fever'
                },
                validation: {},
                value: "",
                valid: true,
                touched: false,
                label: 'Last fever: ',
                show: false,
                loading: false
            },
            respiratoryRate: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Respiratory rate'
                },
                validation: {},
                value: "",
                valid: true,
                touched: false,
                label: 'Respiratory rate: ',
                show: false,
                loading: false
            },
            hypoxia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has hypoxia: ',
                show: false,
                loading: false
            },
            hasSoreThroat: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has sore throat: ',
                show: false,
                loading: false
            },
            hasCough: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has cough: ',
                show: false,
                loading: false
            },
            hasDyspnea: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has dyspnea: ',
                show: false,
                loading: false
            },
            hasTachypnea: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has tachypnea: ',
                show: false,
                loading: false
            },
            alc: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'ALC'
                },
                validation: {},
                value: "",
                valid: true,
                touched: false,
                label: 'Absolute lymphocite count: ',
                show: false,
                loading: false
            },
            pneumonia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has pneumonia: ',
                show: false,
                loading: false
            },
            oxygenSaturation: {
                elementType: 'input',
                elementConfig: {
                    type: 'number',
                    placeholder: 'Oxygen saturation'
                },
                validation: {},
                value: "",
                valid: true,
                touched: false,
                label: 'Oxygen saturation: ',
                show: false,
                loading: false
            },
            nonHospitalPneumonia: {
                elementType: 'input',
                elementConfig: {
                    type: 'checkbox'
                },
                validation: {},
                value: false,
                valid: true,
                touched: false,
                label: 'Has non hospital pneumonia: ',
                show: false,
                loading: false
            },
        },
        countriesVisited: [],
        countriesSelected: [],
        contactedPatients: [],
        patientsSelected: []
    }

    componentDidMount() {
        axios.get("country/get/all")
            .then(res => {
                const countryOptions = res.data.map(country => {
                    return {
                        value: country.id,
                        displayValue: country.countryName
                    };
                })
                const visitedCountries = res.data.map(country => {
                    return {
                        label: country.countryName,
                        value: country.id
                    };
                })
                const updatedForm = { ...this.state.form };
                const updatedElement = { ...updatedForm.countryId };
                const updatedElementConfig = { ...updatedElement.elementConfig };
                updatedElementConfig.options = [...countryOptions];
                updatedElement.elementConfig = updatedElementConfig;
                updatedForm.countryId = updatedElement;
                this.setState({ form: updatedForm, countriesVisited: visitedCountries });
            })
            .catch(err => console.log(err));

        axios.get("api/patient/get/all")
            .then(res => {
                const contactedPatients = res.data.map(p => {
                    return {
                        label: p.name + " " + p.lastName,
                        value: p.id
                    };
                })

                this.setState({contactedPatients: contactedPatients});
            })
    }

    inputChangedHandler = (event, inputIdentifier) => {
        const updatedForm = { ...this.state.form };

        const updatedFormElement = { ...updatedForm[inputIdentifier] };

        if (updatedFormElement.elementConfig.type === "checkbox") {
            updatedFormElement.value = event.target.checked;
        } else {
            updatedFormElement.value = event.target.value;
        }
        // updatedFormElement.valid = checkFormValidity(updatedFormElement.value, updatedFormElement.validation);
        updatedFormElement.touched = true;

        updatedForm[inputIdentifier] = updatedFormElement;

        // let formIsValid = true;
        // for (let input in updatedForm) {
        //     formIsValid = updatedForm[input].valid && formIsValid;
        // }

        this.setState({ form: updatedForm });
    }

    onFormSubmit = (e) => {
        e.preventDefault();

        let formData = {};

        for (let formElementIdentifier in this.state.form) {
            formData[formElementIdentifier] = this.state.form[formElementIdentifier].value;
        }

        const cSelected = this.state.countriesSelected.map(c => c.value);

        const pSelected = this.state.patientsSelected.map(p => p.value);


        formData = { ...formData, countriesVisited: cSelected, contactedPatients: pSelected };

        console.log(formData);

        axios.post('api/getCurringMeasures', formData)
            .then(res => {
                alert("Patient added succesfully.");
                this.props.addPatient(res.data);
                window.location.reload();
            })
            .catch(err => console.log(err));

    }


    render() {

        const formElementsArray = [];

        for (let key in this.state.form) {
            formElementsArray.push({
                id: key,
                config: this.state.form[key]
            });
        }

        let form = (
            formElementsArray.map(formElement => {
                return <Input
                    key={formElement.id}
                    elementType={formElement.config.elementType}
                    elementConfig={formElement.config.elementConfig}
                    value={formElement.config.value}
                    changed={(event) => this.inputChangedHandler(event, formElement.id)}
                    invalid={!formElement.config.valid}
                    shouldValidate={formElement.config.validation}
                    touched={formElement.config.touched}
                    label={formElement.config.label}
                    id={formElement.id}
                />
            })
        );


        return (
            <div className="container">
                <form onSubmit={this.onFormSubmit}>
                    <div className="form-group">
                        <label htmlFor="countriesSelect">Countries visited:</label>
                        <div>
                            <MultiSelect
                                options={this.state.countriesVisited}
                                value={this.state.countriesSelected}
                                onChange={sel => this.setState({ countriesSelected: sel })}
                                labelledBy={"Visited countries"}
                                id="countriesSelect"
                            />
                        </div>
                    </div>

                    <div className="form-group">
                        <label htmlFor="patientsSelect">Patients contacted:</label>
                        <div>
                            <MultiSelect
                                options={this.state.contactedPatients}
                                value={this.state.patientsSelected}
                                onChange={sel => this.setState({ patientsSelected: sel })}
                                labelledBy={"Patients contacted"}
                                id="patientsSelect"
                            />
                        </div>
                    </div>


                    {form}
                    <div className="row">
                        <button className="btn btn-primary" type="submit">Submit</button>
                    </div>
                </form>

            </div>
        );
    }
}

export default NewPatientForm;