import React, { Component } from 'react';
import axios from '../../../axios';

class EditCountryForm extends Component {

    state = {
        countryName: this.props.country.countryName,
        covidPositive: this.props.country.covidPositive,
        idvIndex: this.props.country.idvIndex
    }

    render() {
        this.onSubmit = (e) => {
            e.preventDefault();

            let updatedCountry = { ...this.state, id: this.props.country.id };

            axios.post('country/modify', updatedCountry)
                .then(res => {
                    this.props.updateCountry(res.data);
                })
                .catch(err => console.log(err));

        }

        return (
            <div>
                <form onSubmit={this.onSubmit}>
                    <h5 style={{ textAlign: 'center' }}>Edit Country</h5>
                    <div className="form-group">
                        <label htmlFor="countryNameEdit">Country name</label>
                        <input
                            type="text"
                            className="form-control"
                            id="countryNameEdit"
                            placeholder="Enter country name"
                            value={this.state.countryName}
                            onChange={e => this.setState({ countryName: e.target.value })} />
                    </div>

                    <div className="form-group">
                        <label htmlFor="idvIndexEdit">Country IDV</label>
                        <input
                            type="number"
                            className="form-control"
                            id="idvIndexEdit"
                            placeholder="Enter country IDV index"
                            value={this.state.idvIndex}
                            onChange={e => this.setState({ idvIndex: e.target.value })} />
                    </div>

                    <div className="form-check">
                        <input
                            type="checkbox"
                            className="form-check-input"
                            id="covidPositiveEdit"
                            checked={this.state.covidPositive}
                            onChange={e => this.setState({ covidPositive: e.target.checked })} />
                        <label className="form-check-label" htmlFor="covidPositiveEdit">Country is COVID-19 positive</label>
                    </div>

                    <br />

                    <button type="submit" className="btn btn-primary" style={{ margin: '0px 8px' }}>Accept</button>
                    <button type="button" className="btn btn-warning" style={{ margin: '0px 8px' }} onClick={() => window.location.reload()}>Back</button>
                </form>
            </div>
        );
    }
}

export default EditCountryForm;