import React from 'react';
import classes from './CountryTable.module.css';

const CountryTable = (props) => {

    let table = null;

    if (props.countries) {
        table = (
            <table className="table">
                <thead className="thead-dark">
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">Name</th>
                        <th scope="col">Development</th>
                        <th scope="col">Covid Positive</th>
                    </tr>
                </thead>
                <tbody className={classes.Hover}>
                    {props.countries.map((c, index) => {
                        return <tr key={c.countryName} onClick={() => props.openModal(c)}>
                            <th scope="row">{index + 1}</th>
                            <td>{c.countryName}</td>
                            <td>{c.countryDevelopmentLevel}</td>
                            <td>{c.covidPositive ? "YES" : "NO"}</td>
                        </tr>
                    })}
                </tbody>
            </table>
        );
    }else {
        table = <p>Loading...</p>
    }

    return (
        <div className={classes.Table}>
            {table}
        </div>
    );
};

export default CountryTable;