import React, { Component } from 'react';
import ImageWrapper from '../../components/ImageWrapper/ImageWrapper';
import classes from './Patient.module.css';
import { Link } from 'react-router-dom';
import NewPatientForm from './NewPatientForm/NewPatientForm';
import PatientTable from './PatientTable/PatientTable';
import Modal from '../../components/UI/Modal/Modal';
import EditPatientForm from './EditPatientForm/EditPatientForm';
import axios from '../../axios';

class Patient extends Component {

    state = {
        patients: null,
        modalOpen: false,
        selectedPatient: null
    }

    componentDidMount() {
        axios.get("api/patient/get/all")
            .then(res => {
                console.log(res.data);
                this.setState({patients: res.data});
            })
            .catch(err => console.log(err));
    }

    openModal = (patient) => {
        this.setState({ modalOpen: true, selectedPatient: patient });
    }

    closeModal = () => {
        this.setState({ modalOpen: false });
    }

    addNewPatientHandler = (newPatient) => {
        let patients = [...this.state.patients];
        patients.push(newPatient);
        this.setState({patients: patients});
    }

    render() {

        return (
            <div>
                <ImageWrapper />
                <div className={classes.menu}>
                    <ul>
                        <li><Link to="/">Home</Link></li>
                    </ul>
                </div>

                <hr />

                <div className="container">
                    <div className="row">
                        <div className={classes.Form + " col"}>
                            <h5 style={{ textAlign: "center" }}>Add new Patient</h5>
                            <NewPatientForm addPatient={this.addNewPatientHandler}/>
                        </div>
                        <div className="col">
                            <h5 style={{ textAlign: "center" }}>Patient list</h5>
                            <PatientTable patients={this.state.patients} openModal={this.openModal}/>
                        </div>
                    </div>
                </div>
                <Modal show={this.state.modalOpen} modalClosed={() => this.closeModal()}>
                    {this.state.selectedPatient ? <EditPatientForm patient={this.state.selectedPatient} closeModal={this.closeModal}/> : null}
                </Modal>
            </div>
        );
    }
}

export default Patient;