import React from 'react';
import classes from './PatientTable.module.css';

const PatientTable = (props) => {

    let table = null;

    if (props.patients) {
        table = (
            <table className="table">
                <thead className="thead-dark">
                    <tr>
                        <th scope="col">#</th>
                        <th scope="col">Name</th>
                        <th scope="col">Lastname</th>
                        <th scope="col">Has Covid</th>
                    </tr>
                </thead>
                <tbody className={classes.Hover}>
                    {props.patients.map((p, index) => {
                        return <tr key={p.id} onClick={() => props.openModal(p)}>
                            <th scope="row">{index + 1}</th>
                            <td>{p.name}</td>
                            <td>{p.lastName}</td>
                            <td>{p.covidStatus === "POSITIVE" ? "YES" : "NO"}</td>
                        </tr>
                    })}
                </tbody>
            </table>
        );
    } else {
        table = <p>Loading...</p>
    }


    return (
        <div>
            {table}
        </div>
    );
};

export default PatientTable;